@use "./abstracts/" as *;

.header {
    display: grid;
    grid-auto-flow: column;
    justify-content: space-between;
    align-items: center;
    height: 100px;

    &.container {
        @include media("<=phone") {
            padding: 1rem;
        }

    }
    .brand {
        display: grid;
        grid-auto-flow: column;
        align-items: center;
        gap: 1rem;
        .logo {
            .logo-img {
                width: 3rem;
            }
        }
        .name {
            font-size: 2rem;
            font-weight: 600;

            @include media("<=phone") {
                font-size: 1rem;
            }
        }
    }

    .nav {
        .nav-list {
            display: grid;
            grid-auto-flow: column;
            gap: 3rem;
            .nav-item {
                list-style-type: none;
                font-size: 1.25rem;
                font-weight: 500;

                @include media("<=tablet") {
                    text-align: center;
                }

                .nav-link {
                    text-decoration: none;
                }
            }

            @include media("<=tablet") {
                position: absolute;
                grid-auto-flow: row;
                align-items: center;
                justify-content: center;
                width: 20rem;
                height: 100vh;
                background-color: aquamarine;
                top: 0;
                right: 0;
                transform: translate(20rem);
            }
        }
    }

    .mobile-menu {
        display: none;

        @include media("<=tablet") {
            display: block;
        }
    }
}
